body {
  margin: 0;
font-family: serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2b2b2c;
}

:root {
  --red:     #ff595e;
  --redDark: #f8837c;
  --blue: #0774bb;
  --blueLight: #9AA5F4;
  --orange: #fdee21;
  --orangeDark: #faaf3c;
  --black: #302f2f;
  --fullwidth: 100%;
  --redGradient: linear-gradient(130deg, var(--red), var(--redDark));
  --orangeGradient: linear-gradient(130deg, var(--orangeDark), var(--orange));
  --blueGradient: linear-gradient(130deg, var(--blue), var(--blueLight));
}


::selection {
  color: red;
}

.Navbar {
  height: 90px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 800px;
  width: 100%;
  margin: auto;
}

.Navbar > h4 {
  font-size: 20px;

}

.Navbar > h4:active{
  color: blue;
}

.red:hover  .red,
.red:hover,
.Portfolio:hover ~ #cou
  {
  color: red;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: center;

}


.Header {
  display: flex;
  flex-direction: column;
  font-size: calc(42px + 2vmin);
  text-align: start;
  font-weight: bold;
  position: fixed;
  margin-top: 15px;

}

.Header > p > span{
  line-height: 1.8;
}


.Portfolio {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 1300px;
  justify-content: center;
  width: 100%;
  margin: auto;
  top: 42vh;
  position: relative;

}

.WorkItem {
  width: 480px;
  height: 370px;
  margin: 80px 15px 10px;

}

.Portfolio :hover img{
  transform: scale(1.05);
}

.Portfolio :hover h2:after
{
  content: " \2192";
  transform: scale(1.5);

}



.WorkItem > body{
  height: 70%;
background: #526fff;
  text-align: center;
  padding: 30px;
  display: flex;
  justify-content: center;
 }

.WorkItem > body > img {
  transition: transform .5s ease-out;
}

.WorkItem > .ItemFooter {
  height: 50px;
  line-height: 5px;
  transition: transform .5s ease-out;
}

 .ItemFooter > h2 {
  color: #0000bc;
}

.WorkItem > .ItemFooter > h4 {
  display: inline-block;
line-height: 0px;
  color: #8f8f8f;


}


.dot {
  height: 2px;
  width: 2px;
  background-color: gray;
  border-radius: 50%;
display: inline-block;
  margin: 0 15px 3px 15px;


}

.Contact {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  max-width: 800px;
  top: 40vh;
  align-items: center;

  margin-top: 200px;
  padding-bottom: 100px;
}

.GetContact {
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: row;

}

.GetContact > h2 {
  width: 50%;
  font-size: 36px;
}

.GetContact > h3 {
  width: 50%;
  font-weight: normal;
  line-height: 1.6;
  text-align: start;
margin-top: 2em}


.Footer {
  width: 100%;
  margin-top: 200px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #0000bc;
  font-weight: bold;
  border-top: solid 1px black;
  padding-top: 60px;


}


.Footer > p{
  margin: 20px;
}

.cursor {
  position: fixed;
  left: 10px;
  top: 100px;
  pointer-events: none;
  opacity: 0.3;
  transition: transform .7s ease-out;

}
.cursor--small {
  width: 40px;
  height: 40px;

  border-radius: 50%;
  z-index: 11000;
  background: #e5e5e5;
}



